.formField:not(:first-child) {
  margin-top: 1rem;
}

.label {
  display: block;
}

.input {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  margin-top: 0.5rem;
}

.nameInput {
  composes: input;
}

.descriptionInput {
  composes: input;
  resize: vertical;
}

.priceInput {
  composes: input;
}

.imagesUploader {
  margin-top: 2rem;
}

.submitButton {
  width: 100%;
  margin-top: 1rem;
}

.error {
  color: var(--color-red);
}

.productCategoriesSelect {
  margin-top: 1rem;
}
